.rootf {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  max-width: 75%;
  margin-left: auto;
  margin-right: auto;
}

.contentf {
  text-align: center;
}

.buttonf {
  margin-top: 2;
}

.p22 {
  font-family: 'Comfortaa', cursive;
  font-size: 30px;
  text-align: center;
}
.a22 {
  color: red;
  text-decoration: none;
}
.notfound {
  font-family: 'Comfortaa', cursive;
  font-size: 30px;
  text-align: center;
  background-color: black;
  color: white;
  padding: 15px;
  padding-left: 30px;
  padding-right: 30px;
  font-family: 'Comfortaa', cursive;
  font-size: 30px;
  text-align: center;

  border-radius: 50%;
}
.tipsiz {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  flex-direction: column;
}
.tipsizbody {
  display: flex;
  align-items: center;
  margin: 10px;
}
.arm {
  align-self: flex-end;
  width: 50px;
  height: 90px;
  border-top: 3px solid black;
}
.leftarm {
  align-self: flex-end;
  width: 50px;
  height: 90px;
  border-top: 3px solid black;
  border-right: 3px solid black;
  transform: skew(20deg);
}
.rightarm {
  align-self: flex-end;
  width: 50px;
  height: 90px;
  border-top: 3px solid black;
  border-left: 3px solid black;
  transform: skew(-20deg);
}

.face {
  width: 300px;
  height: 200px;
  border: 3px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-left: 20px;
  margin-right: 20px;
  border-radius: 15px;
}
.upperface {
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 80px;
  margin-left: 80px;
}
.element {
  font-size: 60px;
}
.mouth {
  width: 20px;
  height: 10px;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  border: 3px solid black;
  border-bottom: 0;
}
